// margin.sass
$base-mg-spacing: 10px;
.mg {
    &_btn {
        margin: $base-mg-spacing*2 0px; }
    &_box {
        &--x {
            &10 {
                margin: $base-mg-spacing; }
            &15 {
                margin: $base-mg-spacing + 5; }
            &20 {
                margin: $base-mg-spacing * 2; }
            &30 {
                margin: $base-mg-spacing  * 3; } } }
    &_top {
        &--x {
            &5 {
                margin-top: $base-mg-spacing/2; }
            &10 {
                margin-top: $base-mg-spacing; }
            &15 {
                margin-top: 15px; }
            &20 {
                margin-top: $base-mg-spacing*2; }
            &30 {
                margin-top: $base-mg-spacing*3; }
            &40 {
                margin-top: $base-mg-spacing*5; }
            &50 {
                margin-top: $base-mg-spacing*5; }
            &100 {
                margin-top: $base-mg-spacing * 10;
                &i {
                    @include MQ(S) {
                        margin-top: $base-mg-spacing * 5 !important; }
                    @include MQ(M) {
                        margin-top: $base-mg-spacing * 10 !important; } } }
            &150 {
                margin-top: $base-mg-spacing * 10 + 50;
                &i {
                    @include MQ(S) {
                        margin-top: $base-mg-spacing * 5 + 55 !important; }
                    @include MQ(M) {
                        margin-top: $base-mg-spacing * 10 + 50 !important; } } } } }
    &_bottom {
        &--x {
            &5 {
                margin-bottom: $base-mg-spacing/2; }
            &10 {
                margin-bottom: $base-mg-spacing; }
            &15 {
                margin-bottom: 15px; }
            &20 {
                margin-bottom: $base-mg-spacing*2; }
            &30 {
                margin-bottom: $base-mg-spacing*3; }
            &40 {
                margin-bottom: $base-mg-spacing*4; }
            &50 {
                margin-bottom: $base-mg-spacing*5; }
            &100 {
                margin-bottom: $base-mg-spacing * 10; } } }
    &_left {
        &--x {
            &10 {
                margin-left: $base-mg-spacing; }
            &20 {
                margin-left: $base-mg-spacing * 2; }
            &30 {
                margin-left: $base-mg-spacing * 3; }
            &100 {
                margin-left: $base-mg-spacing * 10; } } }
    &_right {
        &--x {
            &10 {
                margin-right: $base-mg-spacing; }
            &100 {
                margin-right: $base-mg-spacing * 10; } } } }
